@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
    font-family: 'Poppins', sans-serif;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.home {
    display: flex;
    width: 90vw;
    margin: 0 auto;
    height: (100vh - 64px);
    overflow: hidden;
    justify-content: space-between;
    align-items: center;
    margin-top: 64px;
}

.tag-line {
    width: 100%;
    text-align: center;
}

.tag-line h1 {
    font-size: 10vw;
    margin: 0;
}

.tag-line h2 {
    font-weight: 300;
    font-size: 2vw;
    letter-spacing: 0.2em;
    line-height: 1.5;
    
}

.hero-img-wrapper {
    width: 100%;
    height: 100%;
}

.hero-img-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media only screen and (max-width: 676px) {
    .home {
        flex-direction: column;
    }

    .tag-line h2 {
        font-size: 4vw;
    }

    .tag-line h1 {
        margin-bottom: 2em;
    }
}
.sign-in {
    display: flex;
    width: 90vw;
    margin: 0 auto;
    height: (100vh - 64px);
    overflow: hidden;
    justify-content: space-between;
    align-items: center;
    margin-top: 64px;
}

.signin-img-wrapper {
    width: 100%;
    height: 100%;
}

.signin-img-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media only screen and (max-width: 676px) {
    .sign-in {
        flex-direction: column-reverse;
    }
}
.nav-menu {
    background-color: #6C63FF;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-menu ul {
    display: flex;
    width: 80%;
    height: 100%;
    align-items: center;
    justify-content: space-between;
}

.nav-item-group-1 {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.nav-item-group-1 .nav-item:not(:first-child) {
    margin-left: 2em;
    text-align: center;
}

.nav-item-group-2 {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.nav-item-group-2 .nav-item:not(:last-child) {
    margin-right: 1em;
    text-align: center;
}

.nav-item {
    font-size: 1em;
    text-decoration: none;
    cursor: pointer;
}

.nav-header {
    font-size: 1.2em;
    font-weight: 600;
}

